.accordion-navigation {

  > a:after {

    content: "+";

    padding-left: 5px;

    float: right;

    font-size: 20px;
    font-weight: bold;
    padding-right: 8px;


  }

  &.active a:after {

    content: "-";

  }

}

.accordion {
  .accordion-navigation {
    &.active > a, & > a, & > a:hover, a {
      border: 1px solid #fff;
      padding: 11px;
    }

  }

}

.accordion-invers {
  .accordion-navigation {
    &.active > a, & > a, & > a:hover {
      border: 1px solid var(--primary-color);
      background: #fff;
      color: #000;
      padding: 11px;
    }

    > a:after {
      color: var(--primary-color);
    }


  }
}

