input[type="text"],
input[type="password"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="month"],
input[type="week"],
input[type="email"],
input[type="number"],
input[type="search"],
input[type="tel"],
input[type="time"],
input[type="url"],
input[type="color"],
textarea {
	height: 46px;
  border-radius: 2px;
  background-color: #FFF;
  border: 1px solid #e5e5e5;
  margin: 0 0 1.35286rem;
  &:focus {
  	border-color: #83CA13;
  	background-color: #FFF;
  }
}
textarea {
	height: auto;
}


.input {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 100%;
  margin-bottom: 20px;
  vertical-align: top;
}

input.input__field {
  position: relative;
  display: block;
  float: right;
  padding: 0.8em;
  width: 100%;
  border: none;
  border-radius: 0;
  background: #f0f0f0;
  color: #aaa;
  margin-bottom: 0;
  border: 0;
  font-weight: bold;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-appearance: none; /* for box shadows to show on iOS */
}
.select2-container .select2-selection--single {
  height: 46px;
  padding: 9px 0;
  font-size: 16px;
  border-radius: 0;
  border: 1px solid #e5e5e5;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  top: 9px;
  right: 5px;
}
.select2-container--default.select2-container--open.select2-container--below 
.select2-selection--multiple, 
.select2-container--default.select2-container--open.select2-container--below 
.select2-selection--single {
  border-color: #6dc22e;
}

.input__field:focus {
  outline: none;
}

.input__label {
  display: inline-block;
  float: right;
  padding: 0 1em;
  width: 40%;
  font-size: 12px;
  -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.input__label-content {
  position: relative;
  display: block;
  padding: 1.6em 0;
  width: 100%;
}

.graphic {
  position: absolute;
  top: 0;
  left: 0;
  fill: none;
}

.icon {
  color: #ddd;
  font-size: 150%;
}




/* strocked */


input.wd-input-strocked-field {
  width: 100%;
  background: transparent;
  color: #7A7593;
}

.input__label--strocked {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #e5e5e5;
  text-align: left;
  cursor: text;
}

.input__label-content--strocked {
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

.graphic--strocked {
  -webkit-transform: scale3d(1, -1, 1);
  transform: scale3d(1, -1, 1);
  -webkit-transition: stroke-dashoffset 0.3s;
  transition: stroke-dashoffset 0.3s;
  pointer-events: none;

  stroke: #e5e5e5;
  stroke-width: 4px;
  stroke-dasharray: 962;
  stroke-dashoffset: 558;
}

.wd-input-strocked-field:focus + .input__label--strocked,
.input--filled .input__label--strocked {
  cursor: default;
  pointer-events: none;
  padding: 1em;
}

.wd-input-strocked-field:focus + .input__label--strocked .graphic--strocked,
.input--filled .graphic--strocked {
  stroke-dashoffset: 0;
}

.wd-input-strocked-field:focus + .input__label--strocked .input__label-content--strocked,
.input--filled .input__label-content--strocked {
  -webkit-transform: scale3d(0.81, 0.81, 1) translate3d(0, 4em, 0);
  transform: scale3d(0.81, 0.81, 1) translate3d(0, 4em, 0);
}
.input--filled {
  .input__label-content {
    padding: 0;
  }
}
.post-password-form {
  input[type="submit"] {
    padding:10px 20px ;
    color: #fff;
    background: var(--primary-color);
    border: none;
  }
}
.free-quote {
    input[type="text"], 
    input[type="password"], 
    input[type="date"], 
    input[type="datetime"], 
    input[type="datetime-local"], 
    input[type="month"], 
    input[type="week"], 
    input[type="email"], 
    input[type="number"], 
    input[type="search"], 
    input[type="tel"], 
    input[type="time"], 
    input[type="url"], 
    input[type="color"], 
    textarea {
      box-shadow: none;
      border-radius: 30px;
      padding: 10px 0 10px 20px;
      background: rgba(255, 255, 255, 0.79);
      height: 3.7em;
  }
  textarea {
    height: auto;
}
  input.wpcf7-submit{
    box-shadow: none;
    border-radius: 4px;
    padding: 16px 40px;
    transition: .5s ease;
    border: none;
    background: var(--primary-color);
    color:#fff;
    &:hover {
      padding: 16px 50px;
    }
  }
}
