//____________section blog _________________

.wd-section-blog, .wd-section-blog-services {
  article {
    padding-top: 30px;
  }
  margin-bottom:85px;
  .wd-blog-post {
    border: 1px solid #d4dee5;
    margin-bottom: 20px;
  }
  .no-padding .wd-blog-post {
     @include breakpoint(small) {
      border-right-width: 0;
    }
  }
  .no-padding :last-child .wd-blog-post {
    border-right-width: 1px;
  }
  h4 {
    padding: 0 35px;
  }
  p {
    padding: 5px 35px 20px;
  }
  a {
    display: block;
    margin-bottom: 20px;
    padding: 0 35px;
  }
}
.wd-section-blog.style2 {
  .wd-blog-post {
    border: none;
  }
  h4:after {
    content: " ";
    display: block;
    background-color: #82CA13;
    height: 3px;
    width: 40px;
    margin: 20px auto 0;
  }
}
.wd-section-blog-services.style-3 {
  .wd-blog-post {
    margin-top: 30px;
    > div {
      transform: rotate(45deg) translateX(-150px) translateY(-150px);
      transition: all .4s;
      opacity: 0;
      width: 160px;
      height: 160px;
      margin: 0px auto;
      border: 2px solid #F8BA07;
      img {
        transform: rotate(-45deg) scale(2.3) translateY(10px);
      }
    }
    h4:after {
      content: " ";
      display: block;
      background-color:  var(--primary-color);
      height: 3px;
      width: 40px;
      margin: 20px auto 0;
    }

    svg {
      position: absolute;
      top: -6px;
      left: -6px;
    }
    .shape {
      stroke-dasharray: 760;
      stroke-dashoffset: 0;
      stroke-width: 2px;
      fill: transparent;
      stroke: #82CA13;
      border-bottom: 5px solid #000;
      transition: stroke-width 1s ease 0s, stroke-dashoffset 1s ease 0s, stroke-dasharray 1s ease 0s;
    }
    .text {
      font-family: 'Roboto Condensed';
      font-size: 22px;
      line-height: 32px;
      letter-spacing: 8px;
      color: #fff;
      top: -48px;
      position: relative;
    }
    &.nohover .shape {
      stroke-dasharray: 80, 504;
      stroke-dashoffset: -368;
      stroke-width: 8px;
    }
    &:hover .shape {
      stroke-dasharray: 760;
      stroke-dashoffset: 0;
      stroke-width: 2px;
    }
    .img-wrapper {
      overflow: hidden;
      position: absolute;
      top: 0px;
      bottom: 0px;
      width: 100%;
    }
  }
}
.wd-section-blog-services.style-3.anim-on .wd-blog-post > div {
  transform: rotate(0deg) translateX(-15px) translateY(-15px);
  opacity: 1;
}

