 //_____________________section project _____________
 .wd-section-project{
   img {
    width: 100%;
  }
   .wd-title-section_c {
     h2 {
       color: inherit;
     }
   }
   .wd-project {
     text-align: center;
     background:#fff;
     margin-bottom: 28px;
     h4{
       padding:16px 10px 0;
       margin-bottom:0;
       font-size: 19px;
     }
     p {
       padding:0 40px 19px;
       font-size: 13px;
       span:last-child {
        display: none;
       }
     }
   }
   .wd-more-project {
     text-align:center;
     .button{
       padding: 13px 20px;
       font-size:14px;
       i{
         margin-right:9px;
       }
     }
     
   }

   .style-3 {
     height: 500px;
     .column-block {
       padding: 0;
       margin: 0;
       background-size: cover;
       background-position: center;
       height: 100%;
     }
     .portfolio-item {
       position: relative;
       height: 100%;
       &:before {
         content: "";
         display: block;
         position: absolute;
         top: 70%;
         width: 100%;
         height: 30%;
         transition: all .45s ease-out;
         background: linear-gradient(0deg, rgba(0, 0, 0, 0.569), transparent);
       }
       &:after {
        content: "";
        display: block;
        border: 1px solid #fff;
        position: absolute;
        top: 3%;
        width: 94%;
        height: 94%;
        bottom: 3%;
        opacity: 0;
        left: 3%;
        transition: all .35s ease-out;
        transform: scale(.01);
        z-index: 1;
      }
       .portfolio-content {
         display: flex;
         justify-content: center;
         flex-direction: column;
         height: 100%;
         width: 100%;
         transition: all .35s cubic-bezier(0.69, -0.04, 0.12, 1.1);
         justify-content: flex-end;
         padding: 30px;
         position: relative;
         z-index: 3;
         > * {
           z-index: 2;
         }
         h2 {
           font-size: 30px;
           margin: 0;
         }
         a {
           color: #fff;
         }
         .wd-portfolio-category {
           font-size: 16px;
           letter-spacing: .8px;
         }
       }

       &:hover {
         &:before {
           background-color: rgba(0, 0, 0, .5);
           top: 0;
           height: 100%;
         }
         &:after {
          opacity: 1;
          transform: scale(1);
        }
        .portfolio-content {
          transform: translateY(-120px);
        }
       }
     }

     .wd-portfolio-category {
       padding-right: 5px;
     }
   }
 }
